.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  gap: 80px;
  position: relative;
  overflow: hidden;
}
.pullQuestions {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.question {
  cursor: pointer;
}
.dialogs {
  position: absolute;
  background-color: #252525;
  display: flex;
  flex-direction: column;
  width: 350px;
  height: 90vh;
  transition: 1s;
}
.dialogsContent {
  overflow-y: scroll;
  padding-right: 15px;
  padding-left: 15px;
  padding-top: 15px;
}
.dialogsContent::-webkit-scrollbar {
  width: 5px;
}

.dialogsContent::-webkit-scrollbar-track {
  -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
  background-color: #f9f9fd;
  border-radius: 10px;
}

.dialogsContent::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: linear-gradient(180deg, #d9e836, #8a960e);
}
.openDialogsButton {
  position: absolute;
  width: 80px;
  height: 20px;
  border: none;
  border-radius: 10px;
  background: linear-gradient(180deg, #d9e836, #8a960e);
  transition: 1s;
}

@media (max-width: 1000px) {
  .App {
    flex-direction: column;
    gap: 50px;
    height: 100vh;
  }
  .pullQuestions {
    overflow-y: scroll;
    max-height: 50vh;
  }
  .logo {
    display: none;
  }
}
